import { Row, Col, Card } from "react-bootstrap";
import BottleNumbersInBatch from "../batchdetailspage/components/bottleNumbersInBatch";
import COLLarge from "../batchdetailspage/components/ColLarge";
import COLSmall from "../batchdetailspage/components/ContRowCol";
import COLXL from "../batchdetailspage/components/ColXL";
import Description from "../batchdetailspage/components/Description";

export default function PerfumeSection(props) {
  return (
    <>
      {" "}
      <Row className="justify-content-md-center">
        {/* OVERVIEW */}
        <Col
          className="subtitlePadding"
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
        >
          <h1 style={{ color: "white" }}>Perfume</h1>
        </Col>
        <Col xs={12} sm={12} md={12} lg={12}>
          <Card className="Card" style={{ margin: "10px" }}>
            <img src={props.perfumeImage} />
          </Card>
        </Col>

        <COLSmall
          spac="2"
          title="Name"
          component={<p>{props.nameOfPerfume}</p>}
        />

        <COLSmall
          spac="2"
          title="Manufactured by"
          component={<p>Scottish Lavender Oils</p>}
        />

        <COLLarge title="Description" component={props.description} />

        <COLLarge
          title="Perfume Video"
          component={<video controls width="100%" src={props.perfumeVideo} />}
        />

        <COLSmall
          spac="2"
          title={<p>Compounded on {props.dateOfCompounding}</p>}
          component={<img width="100%" src={props.imageCompounding} alt="" />}
        />

        <COLSmall
          spac="2"
          title={<p>Bottled on {props.dateOfBottling}</p>}
          component={<img alt="" width="100%" src={props.imageBottling} />}
        />

        {/* Scent profile */}
        {/* OVERVIEW */}
      </Row>
    </>
  );
}
